@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html, body, #root {
  height: 100%;
}

body {
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}

#app {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

#app-bar {
  display: inline-block;
  padding: 0 8px;

  z-index: 1300;
  background-color: #FFF;
}

#function-input {
  margin-left: 3px;
  margin-right: 75px;

  overflow: hidden;
  -webkit-flex-grow: 1;
          flex-grow: 1
}

#help-button {
  position: absolute;
  right: 0;
}

#function-plot {
  position: absolute;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background: #222;
  overflow: hidden;
}

#function-plot > canvas {
  position: absolute;

  width: 100%;
  height: 100%;

  z-index: 10;
}

#fallback-text {
  position: absolute;
  display: block;

  width: 80%;
  height: 50%;
  margin: auto;

  text-align: center;
  font-size: 40px;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  color: #EEE;

  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;

  z-index: 1;
}

.drawer-content {
  overflow-x: hidden;
}

.control-panel {
  width: 300px;
  height: 100% !important;
  resize: horizontal;
}


/* Media Queries */

@media (max-width: 480px) {
  .control-panel {
    width: 100%;
  }
}

.editable-value {
	color: #777;
	padding: 0;
	border: 0;

	transition: color 0.1s;
}

.editable-value:hover {
	color: #444;
}

.editable-value.focus {
	color: black;
}

.editable-value.invalid {
	color: red;
}

.slider-base {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	white-space: nowrap;

	margin: 16px 0;
	margin-bottom: 24px;
	padding: 0 0 0 25px;

	box-sizing: border-box;
}

.slider {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.slider-base .delete-icon {
	padding: 0;
	margin: auto 8px;
	width: 40px;
	height: 40px;
}

/*** Info Row ***/
.slider .info-row {
	white-space: nowrap;
	display: -webkit-flex;
	display: flex;

	width: 100%;
	height: 20px;
}

.slider .info-row {
	font-size: 14px;
	font-weight: 500;
	font-family: "Roboto", "Helvetica", "Arial", sans-serif;
	line-height: 20px;
}

.slider .info-row .editable-value {
	font-size: 14px;
	font-weight: 500;
	font-family: "Roboto", "Helvetica", "Arial", sans-serif;
	color: black;
}

.slider .info-row .editable-value.invalid {
	color: red;
}

.slider .info-row .name {
	text-align: right;
	margin-right: 3px;
}

.slider .info-row .value {
	margin-left: 3px;
}

/*** Main Row ***/
.slider .main-row {
	white-space: nowrap;
	display: -webkit-flex;
	display: flex;

	width: 100%;
	height: 32px;
}

.slider .main-row .editable-value {
	font-size: 14px;
	font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.slider .main-row .lower-bound {
	text-align: right;
	margin-left: -5px;
}

.slider .main-row .upper-bound {
	margin-right: -5px;
}

.slider .main-row .main-slider {
	margin: auto 10px;
	padding: 0;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}


div.variable-slider {
  width: 90%;
}

#function-editor {
  margin: 20px;
  margin-top: 0;
}

.help-panel {
  width: 580px;
  height: 100% !important;
}

#help-text {
  width: 500px;
  padding: 30px calc(50% - 250px);
}

#help-text p,li {
  font-size: 16px;
  line-height: 1.3;

  text-align: justify;
  text-justify: inter-word;

  font-family: 'Open Sans', sans-serif;

  margin-top: 0;
}

#help-text li {
  margin-bottom: 10px;
}

#help-text h1, h2, h3 {
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
}

#help-text h1 {
  font-weight: 300;
  font-size: 42px;
  color: #555;

  margin-top: 0;
}

#help-text h2 {
  font-weight: 400;
  font-size: 32px;

  margin-top: 30px;
  margin-bottom: 10px;
}

#help-text h3 {
  font-weight: 400;
  font-size: 20px;
  color: #555;

  margin-bottom: 10px;
}

.help-indent {
  margin-left: 20px;
}


/* Media Queries */

@media (max-width: 900px) {
  .help-panel {
    width: 100%;
  }
}

@media (max-width: 600px) {
  #help-text {
    width: calc(100% - 80px);
    padding: 40px;
  }
}

@media (max-width: 480px) {
  #help-text {
    width: calc(100% - 60px);
    padding: 30px;
  }

  #help-text p,li {
    text-align: left;
  }
}

